export default function FundDetail() {
  const params = useParams()
  const fundId = params.fundId as string
  const fund = fundDetails[fundId as keyof typeof fundDetails]

  if (!fund) {
    return (
      <div className="flex flex-col items-center justify-center min-h-[400px] space-y-4">
        <h1 className="text-2xl font-bold">Fund Not Found</h1>
        <p className="text-muted-foreground">The requested mutual fund could not be found.</p>
        <Link href="/mutual-funds">
          <Button>Back to Mutual Funds</Button>
        </Link>
      </div>
    )
  }

  const getRiskColor = (risk: string) => {
    switch (risk) {
      case "Low": return "bg-green-100 text-green-800"
      case "Low to Moderate": return "bg-yellow-100 text-yellow-800"
      case "Moderate": return "bg-orange-100 text-orange-800"
      case "High": return "bg-red-100 text-red-800"
      default: return "bg-gray-100 text-gray-800"
    }
  }

  return (
    <div className="space-y-8">
      {/* Header */}
      <div className="flex flex-col space-y-4">
        <div className="flex items-center space-x-2 text-sm text-muted-foreground">
          <Link href="/mutual-funds" className="hover:text-primary">Mutual Funds</Link>
          <span>/</span>
          <span>{fund.name}</span>
        </div>
        
        <div className="flex flex-col md:flex-row md:items-center md:justify-between space-y-4 md:space-y-0">
          <div>
            <h1 className="text-3xl font-bold tracking-tight">{fund.name}</h1>
            <div className="flex items-center space-x-2 mt-2">
              <Badge variant="outline">{fund.category}</Badge>
              <Badge className={getRiskColor(fund.riskRating)}>
                {fund.riskRating} Risk
              </Badge>
            </div>
          </div>
          
          <Link href={`/invest?fund=${fund.id}`}>
            <Button size="lg" className="w-full md:w-auto">
              Invest Now
            </Button>
          </Link>
        </div>
      </div>

      {/* Key Metrics */}
      <div className="grid gap-4 md:grid-cols-2 lg:grid-cols-4">
        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Annual Return</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold text-green-600">{fund.annualReturn}%</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Fund Size</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{fund.fundSize}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Min Investment</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">₹{fund.minInvestment}</div>
          </CardContent>
        </Card>

        <Card>
          <CardHeader className="pb-2">
            <CardTitle className="text-sm font-medium">Expense Ratio</CardTitle>
          </CardHeader>
          <CardContent>
            <div className="text-2xl font-bold">{fund.expenseRatio}%</div>
          </CardContent>
        </Card>
      </div>

      {/* Main Content Tabs */}
      <Tabs defaultValue="overview" className="space-y-4">
        <TabsList className="grid w-full grid-cols-4">
          <TabsTrigger value="overview">Overview</TabsTrigger>
          <TabsTrigger value="performance">Performance</TabsTrigger>
          <TabsTrigger value="portfolio">Portfolio</TabsTrigger>
          <TabsTrigger value="details">Details</TabsTrigger>
        </TabsList>

        <TabsContent value="overview" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Fund Description</CardTitle>
            </CardHeader>
            <CardContent>
              <p className="text-muted-foreground leading-relaxed">{fund.description}</p>
            </CardContent>
          </Card>

          <Card>
            <CardHeader>
              <CardTitle>NAV Performance (Last 6 Months)</CardTitle>
            </CardHeader>
            <CardContent>
              <ResponsiveContainer width="100%" height={300}>
                <LineChart data={fund.navHistory}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="date" />
                  <YAxis />
                  <Tooltip formatter={(value) => [`₹${value}`, "NAV"]} />
                  <Line type="monotone" dataKey="nav" stroke="#0088FE" strokeWidth={2} />
                </LineChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="performance" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Performance Comparison</CardTitle>
              <CardDescription>Fund performance vs benchmark returns</CardDescription>
            </CardHeader>
            <CardContent>
              <ResponsiveContainer width="100%" height={300}>
                <BarChart data={fund.performanceData}>
                  <CartesianGrid strokeDasharray="3 3" />
                  <XAxis dataKey="period" />
                  <YAxis />
                  <Tooltip formatter={(value) => [`${value}%`, ""]} />
                  <Bar dataKey="fund" fill="#0088FE" name="Fund Return" />
                  <Bar dataKey="benchmark" fill="#00C49F" name="Benchmark Return" />
                </BarChart>
              </ResponsiveContainer>
            </CardContent>
          </Card>
        </TabsContent>

        <TabsContent value="portfolio" className="space-y-4">
          <div className="grid gap-4 md:grid-cols-2">
            <Card>
              <CardHeader>
                <CardTitle>Top Holdings</CardTitle>
                <CardDescription>Top 5 companies in the portfolio</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {fund.topHoldings.map((holding, index) => (
                    <div key={index} className="flex justify-between items-center">
                      <span className="font-medium">{holding.company}</span>
                      <span className="text-muted-foreground">{holding.percentage}%</span>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>

            <Card>
              <CardHeader>
                <CardTitle>Sector Allocation</CardTitle>
                <CardDescription>Investment distribution by sector</CardDescription>
              </CardHeader>
              <CardContent>
                <div className="space-y-3">
                  {fund.sectorAllocation.map((sector, index) => (
                    <div key={index} className="space-y-1">
                      <div className="flex justify-between items-center">
                        <span className="font-medium">{sector.sector}</span>
                        <span className="text-muted-foreground">{sector.percentage}%</span>
                      </div>
                      <div className="w-full bg-gray-200 rounded-full h-2">
                        <div 
                          className="bg-primary h-2 rounded-full" 
                          style={{ width: `${sector.percentage}%` }}
                        ></div>
                      </div>
                    </div>
                  ))}
                </div>
              </CardContent>
            </Card>
          </div>
        </TabsContent>

        <TabsContent value="details" className="space-y-4">
          <Card>
            <CardHeader>
              <CardTitle>Fund Details</CardTitle>
            </CardHeader>
            <CardContent>
              <div className="grid gap-4 md:grid-cols-2">
                <div className="space-y-3">
                  <div>
                    <p className="text-sm text-muted-foreground">Launch Date</p>
                    <p className="font-medium">{fund.launchDate}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Fund Manager</p>
                    <p className="font-medium">{fund.fundManager}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Benchmark</p>
                    <p className="font-medium">{fund.benchmark}</p>
                  </div>
                </div>
                <div className="space-y-3">
                  <div>
                    <p className="text-sm text-muted-foreground">Exit Load</p>
                    <p className="font-medium">{fund.exitLoad}</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Category</p>
                    <p className="font-medium">{fund.category} Fund</p>
                  </div>
                  <div>
                    <p className="text-sm text-muted-foreground">Risk Level</p>
                    <p className="font-medium">{fund.riskRating}</p>
                  </div>
                </div>
              </div>
            </CardContent>
          </Card>
        </TabsContent>
      </Tabs>
    </div>
  )
}
